// 
export default {
  isEmailCaptcha (rule, value, callback) {
    if (!/^\w{6}$/.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  isPhoneCaptcha (rule, value, callback) {
    if (!/^\w{6}$/.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  isPhone (rule, value, callback) {
    if (!/^1\d{10}$/.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  isPassword (rule, value, callback) {
    /* eslint-disable */
    if (!/^(\w|[~`!@#$%^&()\[\]:;"'|\?/><.,{}=*\\+-])+$/.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
    /* eslint-enable */
  },
  equalTo (targetInput) {
    return function (rule, value, callback) {
      if (value !== document.querySelector(targetInput).value.trim()) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
  },
  mustBeTrue (rule, value, callback) {
    if (value !== true) {
      callback(new Error(''))
    } else {
      callback()
    }
  }
}



